              <p
                class="send-message-card-title whatsAppTitle titleText"
                v-text="whatsAppNumbersText"
              ></p>

            </el-col>
            <el-col :span="7" :offset='11'>
              <a
                href="#"
                :style="{ margin: 'auto auto auto 9px' }"
                class="common-href-style"
                @click="
                  goLinkLeads(
                    'https://waplus.io/get-millions-of-whatsapp-group-links-within-2-steps?utm_source=sender_promote'
                  )
                "
              >
                Million Leads For Free!
              </a>
            </el-col>
          </el-row>
            <el-input
              class="textareaBack"
              placeholder="+8617891209984,+8613119140503"
              size="medium"
              v-model="whatsAppNumbersSimple"
              clearable
              slot="reference"
              @focus="handleWhatsAppNumbersFocus"
              @blur="handleWhatsAppNumbersBlur"
              @change="handleWhatsAppNumbersChange"
            >
            </el-input>
            <div class="remindTitle">
                <p class="remindText" v-if="remindTitle==='normal'">Country code in front, separate using comma, like:  +19197646821,+8613119140503 </p>
                <p class="warnText" v-if="remindTitle==='contentCheck'"><i class="exclamation">!</i> Please enter at least <i style="fontWeight:bolder;fontStyle:normal">one</i> number.</p>
                <p class="warnText" v-if="remindTitle==='formatCheck'"><i class="exclamation">!</i> Checking the number's format you entered.
                    <el-tooltip placement="top">
                        <div slot="content">
                            <i class="el-icon-close" style="color:rgba(255, 8, 8, 100);margin-right:3px"></i>
                            <span style="color:#dcdfe6">{{incorrectPhoneNum}}</span>
                            <br/>
                            <i class="el-icon-check" style="color:rgba(79, 251, 106, 100);margin-right:3px"></i>
                            <span style="color:#FFFFFF">+8613119140503</span>
                        </div>
                        <!-- <el-button>Top center</el-button> -->
                        <i class="el-icon-question" style="color:rgba(255, 5, 5, 100)"></i>
                    </el-tooltip>
                </p>

            </div>
        </div>
      </el-card>
    </div>
    <div class="messageText">
      <el-card
        class="box-card message-text"
        shadow="always"
        :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <p class="send-message-card-title titleText" v-text="messageTextText"></p>
        <el-select
          class="send-message-text-select textSelect"
          placeholder="Select template"
          size="small"
          :no-data-text="'No Data'"
          v-model="messageTextTemplateSimple"
          @change="handleMessageTextTemplateChange"
          clearable
        >
          <el-option
            v-for="(item,index) in messageTextTemplateOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
            <template>
                <span @click="selectTemplate(item.label)">{{
                  item.label
                }}</span>
                <i @click.stop.self="handleEdit(index)" class="el-icon-delete"></i>
              </template>
          </el-option>
        </el-select>
        <el-button
          v-text="saveAsTemplateText"
          @click="saveAsTemplateSimpleClick"
          class="send-message-save-template-btn"
          type="success"
          size="small"
        >
        </el-button>
        <el-input
          id="text"
          class="textareaBack textareaForbidResize"
          type="textarea"
          :autosize="{ minRows: 6, maxRows: 6 }"
          v-model="messageTextSimple"
          @change="handleMessageTextChange"
        >
        </el-input>
      </el-card>
    </div>

    <div class="sendingSettings">
      <el-card
        class="box-card sending-setting"
        shadow="always"
        :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <el-row>
          <el-col :span="6">
            <span
              class="send-message-card-title titleText"
              v-text="sendingSettingsText"
            ></span>
          </el-col>
          <el-col :span="16" style="margin: 1px auto">
            <span
              class="send-message-common-font"
              v-text="sendingSettingsNoticeText"
            ></span>
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="6" style="margin-top: 7px">
            <span
              class="send-message-card-title"
              v-text="sendingIntervalText"
              style="color: #c5c5c5"
            ></span>
          </el-col>
          <el-col :span="13">
            <el-input-number
              class="numSelect"
              v-model="minNumSimple"
              :precision="0"
              size="small"
              @change="handleMinNumChange"
              :min="6"
              :max="60"
              label=""
            ></el-input-number>
            ~
            <el-input-number
              class="numSelect"
              v-model="maxNumSimple"
              :precision="0"
              size="small"
              @change="handleMaxNumChange"
              :min="10"
              :max="60"
              label=""
            ></el-input-number>
            <span
              class="send-message-common-font"
              v-text="secondsText"
              style="font-size: 14px; margin-left: 4px"
            ></span>
          </el-col>
          <el-col
            :span="5"
            class="common-success-btn"
            :style="{ textAlign: 'center' }"
          >
            <el-button
              v-if="stopFlagSimple === true"
              type="success"
              @click="sendClick"
              plain
              size="small"
              >Send
            </el-button>
            <el-button
              v-else-if="stopFlagSimple === false"
              disabled
              type="success"
              @click="sendClick"
              plain
              size="small"
              >Sending...
            </el-button>
            <el-button
              v-else-if="stopFlagSimple === 'disabled'"
              disabled
              type="success"
              @click="sendClick"
              size="small"
              class="disableBtn"
              >Send
            </el-button>
          </el-col>
        </el-row>
      </el-card>
    </div>
