<template>
  <div id="app" style="background-color: #F9F9F9">
    <div class="hello">
      <ZbasePopupOnPopup :zbase-method="supportUs" :browserType="browserType"></ZbasePopupOnPopup>
      <TopInfo></TopInfo>
      <el-tabs
          v-model="activeName"
          @tab-click="handleClick"
          class="headerTab"
      >
        <el-tab-pane name="send-messages">
              <span
                  slot="label"
                  style="color: white; padding-left: 16px"
                  v-text="sendMessagesText"
              ></span>
        </el-tab-pane>
        <el-tab-pane name="send-messages-pro">
              <span
                  slot="label"
                  style="color: white"
                  v-text="sendMessagesProText"
              ></span>
        </el-tab-pane>
        <el-tab-pane name="group-tools">
              <span
                  slot="label"
                  style="color: white"
                  v-text="exportGroupText"
              ></span>
        </el-tab-pane>
        <el-tab-pane name="statistics">
              <span
                  slot="label"
                  style="color: white;padding-right:16px"
                  v-text="statisticsText"
              ></span>
        </el-tab-pane>
      </el-tabs>
      <div
          class="tabContainer"
          :class="activeName === 'send-messages' ? 'grey-background-color' : ''"
      >
        <SendMessages
            ref="messageSimple"
            v-if="
                activeName === 'send-messages'
                "
        ></SendMessages>

        <SendMessagesPro
            :groupsMap="groupsMap"
            :groupOptions="groupOptions"
            ref="messagePro"
            v-if="
                activeName === 'send-messages-pro'
                "
        ></SendMessagesPro>

        <GroupTools
            :chooseWarning="chooseWarning"
            :beRemovedWarning="beRemovedWarning"
            :groupsMap="groupsMap"
            :groupOptions="groupOptions"
            v-else-if="
                activeName === 'group-tools'
                "
        ></GroupTools>

        <Statistics
            :isShowContinueBtn="isShowContinueBtn"
            :disabledSendingFlag="disabledSendingFlag"
            :countAll="countAll"
            :countTotal="countTotal"
            :deduplicated="deduplicated"
            :countFail="countFail"
            :countSuccess="countSuccess"
            :waitSeconds="waitSeconds"
            v-else-if="
                activeName === 'statistics'
                "
        ></Statistics>

        <!--  -->
        <el-dialog
            :visible.sync="dialogVisible"
            width="459px"
            top="125px"
            :modal-append-to-body="false"
            :show-close="true"
            :close-on-click-modal="false"
            @open="confirmSendDialogOpen"
            @close="confirmSendDialogClose"
            custom-class="sendConfirmPop"
            center
        >
          <div style="margin-bottom:23px;fontWeight:bold;fontStyle:normal;color:#0f0101">To avoid sending failed , we
            suggest:
          </div>
          <ul class="sendConfirmUl" style="margin:23px 0 35px 15px">
            <li><i class="boldFont">Do not switch the chat window.</i>
              <br>
              <span>Stay here while messages are sending automatically.</span>
            </li>
            <li><i class="boldFont">Keeping networks.</i>
              <br>
              <span>Make sure your phone has an active Internet connection.</span>
            </li>
          </ul>
          <div><a href="#" @click="openWaHelpCenter">About linked devices.</a></div>
          <div class="safetyFooter" slot='footer'>
            <el-button type="success" @click="confirmSendDialog">Agree & Continue</el-button>
            <br>
            <el-checkbox style="margin-top:11px" v-model="notShowAgainModel" @change="notShowAgain">Don't show again</el-checkbox>
          </div>
        </el-dialog>

        <!--  -->
        <el-dialog
            :visible.sync='concatsIsNull'
            width="459px"
            top='220px'
            class='concatsPop'
            :modal-append-to-body='false'
            center>
          <div style="margin-bottom:55px;height:24px;line-height:24px">
            <i class="el-icon-warning" style="color:red;margin:0 16px;fontSize:24px;vertical-align:middle"></i>
            <span style="color:red">Please enter at least one number.  1 number</span>
          </div>
        </el-dialog>

        <!--  -->
        <el-dialog
            :visible.sync="isShowSafetyWarningFlagValue"
            width="459px"
            top='120px'
            custom-class='safetyPop'
            :modal-append-to-body='false'
            :show-close='true'
            :close-on-click-modal="false"
            @open='openSafetyDialog'
            @close='closeSafetyDialog'
            center>
          <div slot="title" style="fontSize:18px;fontWeight:bold;fontStyle:normal;color:#0f0101">Safety Suggestions
          </div>
          <div
              style="margin:-10px 0 0 30px;height:25px;line-height:25px;fontWeight:bold;fontStyle:normal;color:#0f0101">
            To avoid your account being banned, we suggest:
          </div>
          <ul class="safetyFirstUl">
            <li><span>Account has been registered <i class="boldFont"> 15</i> days</span></li>
            <li><span>Mobile phone and website <i class="boldFont">IP are in the same area</i></span></li>
            <li><span><i class="boldFont">Do not</i> switch IP frequently</span></li>
          </ul>
          <div>
            <div style="margin:19px 0 0 30px;height:25px;line-height:25px"><i class="boldFont">Messages you could
              send:</i></div>
            <ul class="safetySecondUl">
              <li><span>50 max - the first day <i class="boldFont">(24h)</i></span></li>
              <li><span>300 max - from second day <i class="boldFont">(>24h)</i></span></li>
            </ul>
          </div>
          <div class="safetyFooter" slot='footer'>
            <el-button type="success" @click="updateShowSafetyWarningFlag">Agree & Continue</el-button>
            <br>
            <el-checkbox style="margin-top:11px" v-model="isDontShowSafetyWarning" @change="againCheckBoxChange">Don't show again</el-checkbox>
          </div>
        </el-dialog>

      </div>
      <CommonBottomHref v-bind:sendType="activeName"></CommonBottomHref>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import CommonBottomHref from "@/components/common//BottomHref";
import {getBrowser} from "@/utils/browser-util";
import {
  WAPLUS_CRM_INSTALL_URL,
  WAPLUS_SENDER_REVIEWS_URL,
} from "@/utils/common/config";

import TopInfo from "@/components/topInfo/TopInfo";
import SendMessages from "@/pages/sendMessages/SendMessages";
import SendMessagesPro from "@/pages/sendMessagesPro/SendMessagesPro";
import GroupTools from "@/pages/groupTools/GroupTools";
import Statistics from "@/pages/statistics/Statistics";
import {ZbasePopupOnPopup} from "zbase-popup-component";

export default {
  name: "App",
  components: {
    CommonBottomHref,
    TopInfo,
    SendMessages,
    SendMessagesPro,
    GroupTools,
    Statistics,
    ZbasePopupOnPopup
  },
  data() {
    return {
      concatsIsNull: false,
      isShowSafetyWarningFlagValue: false,
      isDontShowSafetyWarning: false,
      file: null,
      sendMessagesText: "Send Messages",
      sendMessagesProText: "Send Messages Pro",
      activeName: "send-messages",
      downloadTemplateText: "Excel Template",
      agreeAndContinue: "Agree & Continue",
      sendMessageType: "simple",
      sendMessageStatisticsType: "simple",
      sendSilentSendValue: false,
      groupOptions: [],
      groupsMap: {},

      chooseWarning: false,
      beRemovedWarning: false,
      statisticsText: "Statistics",
      exportGroupText: "Group Tools",
      sleepText: "Help",
      countSuccess: 0,
      countFail: 0,
      countAll: 0,
      countTotal: 0,
      deduplicated: 0,
      waitSeconds: 0,
      firstLoadValue: false,
      selected: "",
      dropdownMap: {
        "User Guide": "https://waplus.io/sender-help",
        "Contact Us":
            "https://api.whatsapp.com/send/?phone=%2B8617891209984&text=Hi+WAPlus+Sender%21+I+have+a+question.",
      },
      dialogVisible: false,
      dailySendNums: 0,
      phoneNumList: [],
      isShowContinueBtn: false,
      loadingPage: null,
      disabledSendingFlag: true,
      firstSendTime: "",
      browserType: "Chrome",
      notShowAgainModel: false
    };
  },
  methods: {
    openWaHelpCenter() {
      window.open('https://faq.whatsapp.com/general/download-and-installation/about-linked-devices')
    },
    confirmSendDialogOpen() {
      chrome.storage.local.set({sendMessageFlag: false})
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300206,{s_type:1})
        }
        if(type === 'pro'){
          _This.$sendLog(300206,{s_type:2})
        }
      })
    },
    confirmSendDialogClose(){
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300207,{s_type:1})
        }
        if(type === 'pro'){
          _This.$sendLog(300207,{s_type:2})
        }
      })
    },
    confirmSendDialog() {
      this.dialogVisible = false
      if(this.notShowAgainModel){
        chrome.storage.local.set({dialogVisible: false});
      }
      const _this = this
      chrome.storage.local.get(['confirmSendData','allDataImg','allDataVideo','allDataDocument','confirmSetData'], res => {
        if (res.confirmSetData) {
          chrome.storage.local.set(res.confirmSetData)
        }
        if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
          let confirmSendData = res.confirmSendData;
          confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
          confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
          confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
          let type = res['confirmSendData']['sendMessageType']
          if(type === 'simple'){
            _this.$sendLog(300208,{s_type:1})
          }
          if(type === 'pro'){
            _this.$sendLog(300208,{s_type:2})
          }
          _this.sendDataPopToContent(confirmSendData)
        }
      })
      chrome.storage.local.set({sendMessageFlag: true})
    },
    handleConfirmClick() {
      this.$nextTick(() => {
        this.sendMessageStatisticsType = this.sendMessageType;
        let content = "";
        let phoneNum = "";
        let mediaType = "";
        let minNum = "";
        let maxNum = "";
        let sentSilent = "";
        let uploadExcel = "";
        let stopFlag = "";
        let stopFlagSimple = "";
        let groupConcats = "";
        const _this = this;
        if (this.sendMessageType === "pro") {
          content = this.$refs.messagePro.messageText;
          phoneNum = this.$refs.messagePro.whatsAppNumbers;
          if (this.$refs.messagePro.radio === "Image") {
            mediaType = "img";
          }
          if (this.$refs.messagePro.radio === "Video") {
            mediaType = "video";
          }
          if (this.$refs.messagePro.radio === "Document") {
            mediaType = "doc";
          }
          if (this.$refs.messagePro.radio === "Contact") {
            mediaType = "contact";
          }
          minNum = this.$refs.messagePro.minNum;
          maxNum = this.$refs.messagePro.maxNum;
          sentSilent = this.sendSilentSendValue;
          uploadExcel = this.$refs.messagePro.uploadExcelValue;
          groupConcats = this.$refs.messagePro.groupConcats;
          stopFlag = false;
          stopFlagSimple = "disabled";
        }
        if (this.sendMessageType === "simple") {
          content = this.$refs.messageSimple.messageTextSimple;
          phoneNum = this.$refs.messageSimple.whatsAppNumbersSimple;
          minNum = this.$refs.messageSimple.minNumSimple;
          maxNum = this.$refs.messageSimple.maxNumSimple;
          stopFlag = "disabled";
          stopFlagSimple = false;
        }
        this.countAll = 0;
        this.countTotal = 0;
        this.deduplicated = 0;
        this.countSuccess = 0;
        this.countFail = 0;
        this.waitSeconds = 0;
        //statisticsprosimple
        const confirmSetData = {
          stopFlag,
          stopFlagSimple,
          sendMessageType: _this.sendMessageType,
          countAll: 0,
          countTotal: 0,
          deduplicated: 0,
          countSuccess: 0,
          countFail: 0,
          activeName: "statistics",
          phoneNumList: [],
          phoneNumSuccessList: [],
          phoneNumFailList: [],
          sendMessageStatisticsType: _this.sendMessageStatisticsType,
          waitSeconds: 0,
        }
        const sendData = {
          phoneNum,
          content,
          mediaType,
          minNum,
          maxNum,
          sentSilent,
          stopFlag,
          stopFlagSimple,
          uploadExcel,
          groupConcats,
          sendMessageType: this.sendMessageType,
          dailySendNums: this.dailySendNums,
        };
        chrome.storage.local.set({
          confirmSendData: sendData,
          confirmSetData: confirmSetData
        })
      });
    },
    notShowAgain(value) {
      let s_status = 1
      if(!value){ s_status = 2 }
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300209,{s_type:1,s_status})
        }
        if(type === 'pro'){
          _This.$sendLog(300209,{s_type:2,s_status})
        }
      })
    },
    fnExport(data) {
      const ws = XLSX.utils.json_to_sheet(data);
      ws["!cols"] = [
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
        {wch: 20},
      ];
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "sheet1");
      XLSX.writeFile(wb, "Send Result.xlsx");
    },
    handleClick(tab, event) {
      if (this.activeName === "send-messages") {
        this.$sendLog(301101);
      }
      if (this.activeName === "send-messages-pro") {
        this.$sendLog(302101);
      }
      if (this.activeName === "group-tools") {
        this.$sendLog(303101);
      }
      if (this.activeName === "statistics") {
        this.$sendLog(304101);
      }
      if (tab.index === "0") {
        this.sendMessageType = "simple";
        chrome.storage.local.set({sendMessageType: this.sendMessageType});
      } else if (tab.index === "1") {
        this.sendMessageType = "pro";
        chrome.storage.local.set({sendMessageType: this.sendMessageType});
      }
      if (tab.index === "4") {
        return;
      } else {
        chrome.storage.local.set({activeName: this.activeName});
      }
    },
    sendDataPopToContent(sendData) {
      let userPhoneNum = ''
      chrome.storage.local.get(['urlStageFlag'])
          .then((res) => {
            if (res.urlStageFlag) {
              chrome.storage.local.remove('urlStageFlag')
              chrome.runtime.sendMessage({urlStageFlag: true, userPhoneNum: userPhoneNum})
            }
          })
      chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, sendData);
      });
    },
    updateShowSafetyWarningFlag() {
      this.isShowSafetyWarningFlagValue = false;
      this.activeName = "statistics";
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300204,{s_type:1})
        }
        if(type === 'pro'){
          _This.$sendLog(300204,{s_type:2})
        }
      })
      if (this.isDontShowSafetyWarning) {
        chrome.storage.local.set({isShowSafetyWarningFlagValue: false});
      } else {
        chrome.storage.local.set({isShowSafetyWarningFlagValue: true});
      }
      chrome.storage.local.get(['dialogVisible', 'confirmSetData', 'confirmSendData','allDataImg','allDataVideo','allDataDocument'], res => {
        if (res.dialogVisible !== false) {
          this.dialogVisible = true
        } else {
          if (res.confirmSetData) {
            chrome.storage.local.set(res.confirmSetData)
          }
          if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
            let confirmSendData = res.confirmSendData;
            confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
            confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
            confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
            this.sendDataPopToContent(confirmSendData)
          }
        }
      })
    },
    sleep(s, e, l) {
      let m = 0;
      if (e >= s) {
        m = Math.floor(Math.random() * (e - s + 1) + s) * l;
      }
      return new Promise((t) => setTimeout(t, m));
    },
    openOrCheckWaTab(event) {
      chrome.tabs.query({currentWindow: true}, function (tabs) {
        if (tabs.length > 0) {
          const urlArr = tabs.filter((item) => item.url);
          let whatsappTab = urlArr.find(
              (e) =>
                  e.url.startsWith("http://web.whatsapp.com/") ||
                  e.url.startsWith("https://web.whatsapp.com/")
          );
          if (typeof whatsappTab !== "undefined") {
            chrome.tabs.highlight(
                {tabs: whatsappTab.index, windowId: whatsappTab.windowId},
                function () {
                  chrome.windows.update(whatsappTab.windowId, {focused: true});
                }
            );
            if (event === "reload") {
              chrome.tabs.reload(whatsappTab.id);
            }
          } else {
            window.open("https://web.whatsapp.com/");
          }
        }
      });
    },

    messageBoxClose() {
      try {
        this.$msgbox.close();
      } catch (err) {
      }
    },
    updateIsShowContinueBtn(isShow) {
      this.isShowContinueBtn = isShow;
      chrome.storage.local.set({isShowContinueBtn: isShow});
    },
    updateDisabledSendingFlag(isDisabled) {
      this.disabledSendingFlag = isDisabled;
      chrome.storage.local.set({disabledSendingFlag: isDisabled});
    },
    supportUs(phone) {
      chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {openChat: phone});
      })
    },
    openSafetyDialog(){
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300202,{s_type:1})
        }
        if(type === 'pro'){
          _This.$sendLog(300202,{s_type:2})
        }
      })
    },
    closeSafetyDialog(){
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300203,{s_type:1})
        }
        if(type === 'pro'){
          _This.$sendLog(300203,{s_type:2})
        }
      })
    },
    againCheckBoxChange(value){
      let s_status = 1
      if(!value){ s_status = 2 }
      let _This = this
      chrome.storage.local.get('confirmSendData',function (res) {
        let type = res['confirmSendData']['sendMessageType']
        if(type === 'simple'){
          _This.$sendLog(300205,{s_type:1,s_status})
        }
        if(type === 'pro'){
          _This.$sendLog(300205,{s_type:2,s_status})
        }
      })
    }
  },
  created: async function () {
    this.browserType = getBrowser()['browser']
    let _This = this;
    chrome.tabs.query({active: true, currentWindow: true}, function (tab) {
      let tablink = tab[0].url;
      var re = /^(http|https):\/\/web.whatsapp.com\/[^\s]*/;
      if (!re.test(tablink)) {
        _This.openOrCheckWaTab();
      }
    }),
        chrome.storage.local.get(['allDataImg','allDataVideo','allDataDocument','sendMessageFlag', 'confirmSendData', 'activeName', 'firstSendTime', 'userPhoneNum', 'installEventFlag', 'sendSilentSendValue', 'countAll', 'countTotal', 'deduplicated', 'countSuccess', 'countFail', 'waitSeconds', 'chooseWarning', 'beRemovedWarning', 'groupOptions', 'dialogVisible', 'dailySendNums', 'sendMessageType', 'phoneNumList', 'disabledSendingFlag', 'isShowContinueBtn'], function (res) {
          if (res.sendMessageFlag === false) {
            let confirmSendData = res.confirmSendData;
            confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
            confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
            confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
            _This.sendDataPopToContent(confirmSendData)
            chrome.storage.local.set({sendMessageFlag: true})
          }
          _This.activeName = res.activeName || "send-messages";
          _This.firstSendTime = res.firstSendTime || "";

          _This.$sendLog(300001);

          // if (
          //   !res.inviteCommentFlag &&
          //   Math.round(new Date() / 1000) - res.inviteCommentTime > 86400
          // ) {
          //   _This.openInviteCommentMessageBox();
          // }
          // if (
          //   res.inviteCommentFlag  &&
          //   Math.round(new Date() / 1000) - res.inviteCommentEndTime > 86400 &&
          //   (!res.recommendCommentEndTime ||
          //       Math.round(new Date() / 1000) - res.recommendCommentEndTime >
          //       15 * 86400) &&
          //   !res.extensionCrm
          // ) {
          //   _This.openRecommendCommentMessageBox();
          // }
          if (res.userPhoneNum === undefined) {
            _This.firstLoadValue = true;
            _This.$alert({});
            _This
                .$confirm(
                    "<p>Failed to connect to your WhatsApp, please refresh the page to reconnect.</p><br>" +
                    "<p>Otherwise, WAPlus Sender will not work.</p>",
                    "",
                    {
                      customClass: "firstLoad",
                      showConfirmButton: true,
                      confirmButtonText: "Reconnect",
                      showClose: false,
                      showCancelButton: false,
                      dangerouslyUseHTMLString: true,
                      type: "warning",
                    }
                )
                .then(() => {
                  _This.openOrCheckWaTab("reload");
                });
          } else {
            chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
              chrome.tabs.sendMessage(tabs[0].id, {msgFlag: true}, res => {
                if (!res) {
                  _This.$sendLog(300009)
                  _This
                      .$confirm(
                          "<p>Failed to connect to your WhatsApp, please refresh the page to reconnect.</p><br>" +
                          "<p>Otherwise, WAPlus Sender will not work.</p>",
                          "",
                          {
                            customClass: "firstLoad",
                            showConfirmButton: true,
                            confirmButtonText: "Reconnect",
                            showClose: false,
                            showCancelButton: false,
                            dangerouslyUseHTMLString: true,
                            type: "warning",
                          }
                      )
                      .then(() => {
                        _This.openOrCheckWaTab("reload");
                      });
                }
              });
            });
            if (!res.installEventFlag) {
              const browserData = {
                browserInfo: getBrowser(),
                platform: window.navigator.platform,
                language: window.navigator.language,
                phoneNum: res.userPhoneNum,
              };
              chrome.storage.local.set({
                installEventFlag: true,
                ...browserData,
              });
              _This.$sendLog(300005, browserData);
            }
          }
          _This.sendSilentSendValue = res.sendSilentSendValue;
          let sendData = {addJsSource: true};
          _This.sendDataPopToContent(sendData);
          _This.sendDataPopToContent({queryAllGroups: true});
          _This.countAll = res.countAll ? res.countAll : 0;
          _This.countTotal = res.countTotal ? res.countTotal : 0;
          _This.deduplicated = res.deduplicated ? res.deduplicated : 0;
          _This.countSuccess = res.countSuccess ? res.countSuccess : 0;
          _This.countFail = res.countFail ? res.countFail : 0;
          _This.waitSeconds = res.waitSeconds ? res.waitSeconds : 0;
          _This.chooseWarning = res.chooseWarning ? res.chooseWarning : false;
          _This.beRemovedWarning = res.beRemovedWarning ? res.beRemovedWarning : false;
          _This.groupOptions =
              res.groupOptions !== undefined ? res.groupOptions : [];
          _This.dialogVisible =
              res.dialogVisible !== undefined ? res.dialogVisible : false;
          _This.dailySendNums =
              res.dailySendNums !== undefined ? res.dailySendNums : {};
          _This.sendMessageType =
              res.sendMessageType !== undefined ? res.sendMessageType : "simple";
          _This.phoneNumList =
              res.phoneNumList !== undefined ? res.phoneNumList : [];
          _This.disabledSendingFlag =
              res.disabledSendingFlag !== undefined
                  ? res.disabledSendingFlag
                  : false;
          _This.isShowContinueBtn =
              res.isShowContinueBtn !== undefined ? res.isShowContinueBtn : false;
          const {isReachSendLimit, showConfirmTitle} =
              _This.$checkDailySendNums(res.dailySendNums);
          if (isReachSendLimit) {
            _This.$confirm(showConfirmTitle, "", {
              confirmButtonText: "OK",
              type: "warning",
              showClose: false,
              showCancelButton: false,
              customClass: "daily-msg-send-warning",
            });
          }
        });
  },
  computed: {
    pauseAndSendBtn() {
      return {
        countAll: this.countAll,
        countSuccess: this.countSuccess,
        countFail: this.countFail,
        waitSendNum: this.countAll - this.countSuccess,
      };
    },
    disabledSendingFlagData() {
      return {
        countAll: this.countAll,
        waitSeconds: this.waitSeconds,
      };
    },
  },
  watch: {
    isShowSafetyWarningFlagValue: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          let userPhoneNum = ''
          chrome.storage.local.set({
            urlStageFlag: 1
          })
          chrome.storage.local.get(['phoneNum'], res => {
            userPhoneNum = res.phoneNum || ''
            chrome.runtime.sendMessage({urlStageFlag: true, userPhoneNum})
          })
        }
      }
    },
    dialogVisible: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          let userPhoneNum = ''
          chrome.storage.local.set({
            urlStageFlag: 2
          })
          chrome.storage.local.get(['phoneNum'], res => {
            userPhoneNum = res.phoneNum || ''
            chrome.runtime.sendMessage({urlStageFlag: true, userPhoneNum})
          })
        }
      }
    },
    pauseAndSendBtn: {
      handler(newData, oldData) {
        if (newData.countAll === newData.countSuccess + newData.countFail) {
          this.updateIsShowContinueBtn(true);
          return;
        }
        if (
            newData.countAll === newData.countSuccess &&
            newData.countAll === 0
        ) {
          this.updateIsShowContinueBtn(false);
          return;
        }
        if (newData.countAll > newData.countSuccess) {
          if (
              oldData.countAll === 0 &&
              oldData.countFail === 0 &&
              oldData.countSuccess === 0 &&
              oldData.waitSendNum === 0
          ) {
            if (newData.countAll === newData.waitSendNum - newData.countFail) {
              this.updateIsShowContinueBtn(false);
            }
            return;
          }
          this.updateIsShowContinueBtn(false);
          return;
        }
        this.updateIsShowContinueBtn(true);
      },
      deep: true,
    },
    disabledSendingFlagData: {
      handler(newValue, oldValue) {
        if (
            oldValue.countAll === 0 &&
            oldValue.waitSeconds === 0 &&
            newValue.countAll > 0
        ) {
          this.updateDisabledSendingFlag(true);
        }
      },
      immediate: true,
    },
    waitSeconds: {
      handler(newValue, oldValue) {
        if (newValue === 0) {
          this.updateDisabledSendingFlag(true);
          this.messageBoxClose();
        }
        if (oldValue === 0 && newValue > 0) {
          this.updateDisabledSendingFlag(false);
        }
      },
      immediate: true,
    },
  },
  mounted() {
    let _This = this;
    chrome.runtime.onMessage.addListener(function (
        message,
        sender,
        sendResponse
    ) {
      if (message.stopFlag) {
        _This.$nextTick(() => {
          _This.$refs.messagePro.stopFlag = message.stopFlag
        })
        chrome.storage.local.set({stopFlag: true});
      }
      if (message.stopFlagSimple) {
        _This.$nextTick(() => {
          _This.$refs.messageSimple.stopFlagSimple = message.stopFlagSimple
        })
        chrome.storage.local.set({stopFlagSimple: message.stopFlagSimple});
      }
      if (message.countAll) {
        _This.countAll = message.countAll;
      }
      if (message.countTotal) {
        _This.countTotal = message.countTotal;
      }
      if (message.deduplicated) {
        _This.deduplicated = message.deduplicated;
      }
      if (message.countSuccess) {
        _This.countSuccess = message.countSuccess;
      }
      if (message.countFail) {
        _This.countFail = message.countFail;
      }
      if (message.waitSeconds !== undefined) {
        _This.waitSeconds = message.waitSeconds;
      }
      if (message.chooseWarning !== undefined) {
        _This.chooseWarning = message.chooseWarning;
      }
      if (message.beRemovedWarning !== undefined) {
        _This.beRemovedWarning = message.beRemovedWarning;
      }
      if (message.groupsInfo !== undefined) {
        let newGroupOptions = [];
        let newGroupsMap = {};
        message.groupsInfo.forEach((item, i) => {
          newGroupOptions.push({
            data: item.data,
            key: item.key,
            value: item.key,
            label: item.label,
          });
          newGroupsMap[item.key] = item.data;
        });
        _This.groupOptions = newGroupOptions;
        _This.groupsMap = newGroupsMap;
        chrome.storage.local.set({groupOptions: newGroupOptions});
        chrome.storage.local.set({groupsMap: newGroupsMap});
      }
      if (message.reloadPopup) {
        window.location.reload();
        this.updateIsShowContinueBtn(false);
      }
      if (message.isReachSendLimit) {
        _This.$confirm(message.showConfirmTitle, "", {
          confirmButtonText: "OK",
          type: "warning",
          showClose: false,
          showCancelButton: false,
          customClass: "daily-msg-send-warning",
        });
      }
      if (message.incrDailySendNums !== undefined) {
        _This.dailySendNums = message.incrDailySendNums;
      }
      if (message.phoneNumList) {
        _This.phoneNumList = message.phoneNumList;
      }
      if (message.disabledSendingFlag !== undefined) {
        _This.updateDisabledSendingFlag(message.disabledSendingFlag);
      }
      sendResponse();
      return true;
    });
  },
};
</script>

