import _ from "lodash";
import {browseDocumentFile, browseImageFiles, browseVideoFile} from "@/utils/base64";

export default {
  name: "SendMessagesPro",
  props: ["groupOptions", "groupsMap"],
  data() {
    return {
      sendToGroupText: 'Send to group members 1 by 1',
      whatsAppNumbersText: "WhatsApp Numbers",
      whatsAppNumbersExcelText: "WhatsApp Number's Excel",
      uploadExcelValue: true,
      excelNumbersText: "",
      excelData: [],
      uploadExcelText: "Upload Excel",
      usePlaceholderValue: false,
      placeholderTemplateValue: "value1",
      downloadTemplateText: "Excel Template",
      whatsAppNumbers: "",
      //   isDisabledGroupSelector: false,
      groupInfo: "",
      messageTextText: "Message Text",
      messageTextTemplate: "",
      messageTextTemplateOptions: [],
      sendMessagePro: "pro",
      messageText: "",
      saveAsTemplateText: "Save as template",
      sendAttachmentsValue: false,
      radio: "",
      attachmentText:
          'The attachment will be sent to your own account first, then open WA Plus Sender, click the "Send" button.',
      apologizeForBotherValue: false,
      placeholderTemplateOptions: [
        {
          value: "value1",
          label: "WhatsApp Number",
        },
        {
          value: "value2",
          label: "First Name",
        },
        {
          value: "value3",
          label: "Last Name",
        },
        {
          value: "value4",
          label: "Other",
        },
      ],
      sendingSettingsText: "Sending Settings",
      sendingSettingsNoticeText:
          "Messages will be sent randomly from this interval.",
      sendingIntervalText: "Sending Interval",
      minNum: 6,
      maxNum: 10,
      secondsText: "seconds",
      stopFlag: true,
      remindTitlePro: 'normal',
      //   remindTitlePro:'contentCheck',
      incorrectPhoneNumPro: '',
      inputPhoneNum: 0,
      inputPhoneNumFlag: 'normal',
      groupConcats: 'normal',
      groupMembersNum: '',
      proChangeNum: 0,
      contentArrPro: [],
      incorrectPhoneNumArrPro: [],
      file: {},
      attachmentDivHtml: ''
    };
  },
  methods: {
    howToUse() {
      window.open('https://waplus.io/sender-help')
    },
    selectedTemplateChange(value) {
      if (value) {
        let templateNode = document.querySelectorAll('.el-select-dropdown__item')
        let templateContent = document.querySelectorAll('.el-select-dropdown__item > span')
        templateNode.forEach(item => {
          item.style.width = '372px'
        })
        templateContent.forEach(item => {
          item.style.width = '352px'
        })
      }
    },
    uploadExcelClick(file, fileList) {
      if (fileList.length > 1) {
        fileList.splice(0, 1)
      }
      this.readExcel(file)
          .then((item) => {
            if (item && item.length > 0) {
              if (item[0] && item[0].sheet && item[0].sheet.length) {
                let excelData = item[0].sheet;
                excelData = this.dealExcelData(excelData);
                console.log(excelData)
                this.excelNumbersText = excelData.length;
                //   chrome.storage.local.set({ excelData: excelData });
                this.uploadExcelValue = false;
                chrome.storage.local.set({excelData: excelData, uploadExcelValue: false});
                if (excelData.length === 0) {
                  this.$sendLog(302302, {s_status: 2});
                } else {
                  this.$sendLog(302302, {s_status: 1});
                }
              } else {
                this.excelNumbersText = 0
                this.uploadExcelValue = false
                chrome.storage.local.set({excelData: [], uploadExcelValue: false});
              }
            }
          })
          .catch((error) => {
            console.log(error);
          });
    },
    readExcel(file) {
      return new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: "binary",
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      });
    },
    dealExcelData(excelData) {
      let result = [];
      for (let i = 0; i < excelData.length; i++) {
        let dataTmp = excelData[i];
        let data = {};
        //excel+
        data = this.checkSpecialCharacters(
            dataTmp,
            data,
            "WhatsApp Number(with country code)"
        );
        data = this.checkSpecialCharacters(dataTmp, data, "First Name");
        data = this.checkSpecialCharacters(dataTmp, data, "Last Name");
        data = this.checkSpecialCharacters(dataTmp, data, "Other");
        let whatsAppNumber = data["WhatsApp Number(with country code)"];
        let firstName = data["First Name"];
        let lastName = data["Last Name"];
        let other = data["Other"];
        if (whatsAppNumber) {
          let tmp = {};
          tmp["whatsAppNumber"] = whatsAppNumber;
          tmp["firstName"] = firstName;
          tmp["lastName"] = lastName;
          tmp["other"] = other;
          result.push(tmp);
        }
      }
      return result;
    },
    uploadExcelClickLog() {
      this.$sendLog(302201);
    },
    deleteGroupClick() {
      // this.$confirm(
      //     "All group numbers will be deleted. Do you want to continue?",
      //     "Warning",
      //     {
      //       confirmButtonText: "Delete",
      //       cancelButtonText: "Cancel",
      //       confirmButtonClass: "confirmButton",
      //       type: "warning",
      //     }
      // )
      //     .then(() => {
      //       chrome.storage.local.set({groupConcats: 'normal'});
      //       this.groupConcats = 'normal';
      //       this.groupInfo = "";
      //       this.groupMembersNum = ''
      //       chrome.storage.local.remove(["groupMembersNum", "groupInfo"]);
      //     })
      //     .catch(() => {
      //     });
      chrome.storage.local.set({groupConcats: 'normal'});
      this.groupConcats = 'normal';
      this.groupInfo = "";
      this.groupMembersNum = ''
      chrome.storage.local.remove(["groupMembersNum", "groupInfo"]);
    },
    deleteExcelClick() {
      // this.$confirm(
      //     "All numbers and placeholders will be deleted. Do you want to continue?",
      //     "Warning",
      //     {
      //       confirmButtonText: "Delete",
      //       cancelButtonText: "Cancel",
      //       confirmButtonClass: "confirmButton",
      //       type: "warning",
      //     }
      // )
      //     .then(() => {
      //       chrome.storage.local.remove("excelData");
      //       this.uploadExcelValue = true;
      //       this.usePlaceholderValue = false;
      //       this.placeholderTemplateValue = "value1";
      //       chrome.storage.local.set({uploadExcelValue: true});
      //     })
      //     .catch(() => {
      //     });
      chrome.storage.local.remove("excelData");
      this.uploadExcelValue = true;
      this.usePlaceholderValue = false;
      this.placeholderTemplateValue = "value1";
      chrome.storage.local.set({uploadExcelValue: true});
    },
    downloadTemplateClick() {
      this.$sendLog(302203);
      const a = document.createElement("a");
      a.setAttribute("download", "WAPlusSenderTemplate.xlsx");
      a.setAttribute("href", 'https://scrm-data-us-oss.oss-us-west-1.aliyuncs.com/sender/static_resources/WAPlusSenderTemplate.xlsx');
      a.click();
    },
    handleWhatsAppNumbersFocus() {
      let remindBorder = document.querySelector('.textareaBackPro.el-input.el-input--medium.el-input--suffix>.el-input__inner')
      this.remindTitlePro = 'normal'
      this.inputPhoneNumFlag = 'noraml'
      remindBorder.style.borderColor = 'rgb(220, 223, 230)'
    },
    handleWhatsAppNumbersBlur(e) {
      this.handleWhatsAppNumbersChange(e.target.value)
    },
    handleWhatsAppNumbersChange(value) {
      if (value) {
        value = value.trim()
        this.inputPhoneNumFlag = 'content'
        phoneNumNormalPro(this)
        phoneNumFormatCheckPro(value, this)
        if (this.remindTitlePro !== 'normal') {
          this.$sendLog(302308, {
            s_content: this.contentArrPro,
            s_incorrect_phone_num: this.incorrectPhoneNumArrPro,
            s_status: 2
          })
        }
      } else {
        this.inputPhoneNumFlag = 'normal'
        phoneNumNormalPro(this)
        this.$sendLog(302308, {s_content: [], s_incorrect_phone_num: [], s_status: 1})
        this.inputPhoneNum = 0
        chrome.storage.local.set({inputPhoneNum: 0})
      }
      this.proChangeNum++
      chrome.storage.local.set({
        whatsAppNumbers: this.whatsAppNumbers,
        inputPhoneNumFlag: this.inputPhoneNumFlag,
        proChangeNum: this.proChangeNum - 1
      });
    },
    // handleDisabledGroupSelector() {
    //   console.log("isDisabledGroupSelector:" + this.isDisabledGroupSelector);
    // },
    handleGroupChange() {
      this.groupConcats = 'content'
      this.$parent.messageBoxClose();
      let phoneNumberStr = "";
      let group = this.groupsMap[this.groupInfo];
      chrome.storage.local.set({groupInfo: this.groupInfo})
      let phoneNumberList = getAllPhoneNumberList(group);
      phoneNumberList.forEach((item, i) => {
        phoneNumberStr += "+" + item;
        if (i < phoneNumberList.length - 1) {
          phoneNumberStr += ",";
        }
      });
      //   this.$parent.messageBoxClose();
      if (phoneNumberStr.length === 0) {
        this.groupMembersNum = 0
        this.groupConcats = 'content'
        chrome.storage.local.set({
          groupMembersNum: 0,
          groupConcats: 'content',
          groupMembersPhoneList: phoneNumberList
        })
      } else {
        this.groupMembersNum = phoneNumberList.length
        chrome.storage.local.set({
          groupMembersNum: phoneNumberList.length,
          groupConcats: 'content',
          groupMembersPhoneList: phoneNumberList
        })
        addGroupPhoneNumList(phoneNumberStr);
        // this.$confirm("", "Group members have been successfully extracted.", {
        //   customClass: "emptyContent",
        //   showConfirmButton: false,
        //   showCancelButton: false,
        //   center: true,
        //   type: "success",
        // });
      }
    },
    handleGroupChangeLog(value) {
      if (value) {
        this.$sendLog(302202);
        if (this.groupOptions.length === 0) {
          this.$sendLog(302303, {s_status: 2});
        } else {
          this.$sendLog(302303, {s_status: 1});
          let templateNode = document.querySelectorAll('.el-select-dropdown__item')
          let templateContent = document.querySelectorAll('.el-select-dropdown__item > span')
          templateNode.forEach(item => {
            item.style.width = '100%'
          })
          templateContent.forEach(item => {
            item.style.width = '100%'
          })
        }
      }
    },
    handleMessageTextTemplateChange(value) {
      this.messageText = value;
      chrome.storage.local.set({
        messageText: this.messageText,
        messageTextTemplate: this.messageTextTemplate,
      });
    },
    handleEdit(index) {
      this.messageTextTemplateOptions.splice(index, 1)
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions,
      });
    },
    selectTemplate(value) {
      this.messageText = value;
      chrome.storage.local.set({
        messageText: this.messageText,
        messageTextTemplate: this.messageTextTemplate,
      });
    },
    saveAsTemplateClick() {
      this.$sendLog(302204);
      if (this.messageText.replace(/\s/g, "").length) {
        let tmp = {
          value: this.messageText,
          label: this.messageText,
        };
        let flag = 0;
        for(let templates of this.messageTextTemplateOptions){
          if (templates.value === tmp.value)
            flag = 1
        }
        if(flag){
          this.$confirm("", 'The template has been saved.', {
            customClass: "emptyContentTemplate",
            showConfirmButton: false,
            showCancelButton: false,
            center: true,
            type: "warning"
          })
        }else {
          document.getElementsByClassName('saveTemplateBtn')[0].style.setProperty('animation','none')
          document.getElementsByClassName('saveTemplateBtn')[0].style.setProperty('-webkit-animation','none')
          setTimeout(function () {
            document.getElementsByClassName('saveTemplateBtn')[0].style.setProperty('animation','mymove 1s')
            document.getElementsByClassName('saveTemplateBtn')[0].style.setProperty('-webkit-animation','mymove 1s')
            const keyframes1 = '@keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const keyframes2 = '@-webkit-keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const style = document.createElement('style');
            style.type = 'text/css';
            style.innerHTML = keyframes1;
            document.getElementsByClassName('saveTemplateBtn')[0].appendChild(style);
            style.innerHTML = keyframes2;
            document.getElementsByClassName('saveTemplateBtn')[0].appendChild(style);
          },1)
          this.messageTextTemplateOptions.push(tmp);
        }
      }
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions,
      });
    },
    handleMessageTextChange(value) {
      chrome.storage.local.set({messageText: value});
    },
    handleAttachmentsChange(value) {
      if (value) {
        this.$sendLog(302304);
        chrome.storage.local.set({
          sendAttachmentsValue: this.sendAttachmentsValue,
        });
      } else {
        this.radio = "";
        this.sendAttachmentsValue = false;
        chrome.storage.local.set({
          sendAttachmentsValue: this.sendAttachmentsValue,
          radio: this.radio,
          attachmentDivHtml: '',
          allDataImg: [],
          allDataVideo: [],
          allDataDocument: []
        });
      }
    },
    handleImgDocChange(value) {
      let mediaType = "";
      if (this.radio === "Image") {
        this.$sendLog(302305, {s_status: 1});
        mediaType = "img";
        browseImageFiles(this);
      }
      if (this.radio === "Video") {
        this.$sendLog(302305, {s_status: 2});
        mediaType = "video";
        browseVideoFile(this);
      }
      if (this.radio === "Document") {
        this.$sendLog(302305, {s_status: 3});
        mediaType = "doc";
        browseDocumentFile(this)
      }
      if (this.radio === "Contact") {
        this.$sendLog(302305, {s_status: 4});
        mediaType = "contact";
      }
      chrome.storage.local.set({
        sendAttachmentsValue: this.sendAttachmentsValue,
        radio: this.radio,
      });
      const sendData = {
        sendAttachments: true,
        mediaType: mediaType
      };
      this.$parent.sendDataPopToContent(sendData);
    },
    attachmentBtn(flag) {
      if (flag === "img") {
        this.radio === "Image";
      } else if (flag === "video") {
        this.radio === "Video";
      } else if (flag === "doc") {
        this.radio === "Document";
      } else {
        this.radio === "Contact";
      }
    },
    handleApologizeForBotherChange(value) {
      if (value) {
        this.$sendLog(302306);
        let tmpText =
            'If it disturbs you, please reply "No" and I will not send you any more messages.';
        this.messageText = this.messageText + tmpText;
      }
      chrome.storage.local.set({
        messageText: this.messageText,
        apologizeForBotherValue: this.apologizeForBotherValue,
      });
    },
    handleUsePlaceholderChange(value) {
      this.placeholderTemplateValue = "value1";
      if (value) {
        this.$sendLog(302307);
        let insertText = "{{WhatsApp Number}}";
        this.insert(insertText);
      }
    },
    insert(value) {
      const textField = document.getElementById("text");
      if (textField.selectionStart || textField.selectionStart === 0) {
        let startPos = textField.selectionStart;
        let endPos = textField.selectionEnd;
        let content =
            textField.value.substring(0, startPos) +
            value +
            textField.value.substring(endPos, textField.value.length);
        this.messageText = content;
        textField.focus();
        textField.setSelectionRange(
            endPos + value.length,
            endPos + value.length
        );
      } else {
        this.messageText += "" + value;
      }
      chrome.storage.local.set({messageText: this.messageText});
    },
    handlePlaceholderTemplateChange(value) {
      if (value === "value1") {
        this.insert("{{WhatsApp Number}}");
      }
      if (value === "value2") {
        this.insert("{{First Name}}");
      }
      if (value === "value3") {
        this.insert("{{Last Name}}");
      }
      if (value === "value4") {
        this.insert("{{Other}}");
      }
    },
    handleMinNumChange(value) {
      if (value >= this.maxNum) {
        this.$nextTick(() => {
          this.minNum = this.maxNum;
          chrome.storage.local.set({minNum: this.minNum});
        });
      }
    },
    handleMaxNumChange(value) {
      if (value <= this.minNum) {
        this.$nextTick(() => {
          this.maxNum = this.minNum;
          chrome.storage.local.set({maxNum: this.maxNumSimple});
        });
      }
    },
    async sendClick() {
      if (!this.$parent.firstSendTime) {
        chrome.storage.local.set({
          firstSendTime: Math.round(new Date() / 1000),
        });
      }
      this.$sendLog(302205);
      this.$parent.updateIsShowContinueBtn(true);
      let content
      if (this.messageText) {
        content = this.messageText.trim()
      } else {
        content = this.messageText
      }
      if (!content) {
        this.$confirm("", "Please enter a message or upload an attachment.", {
          customClass: "emptyContent",
          showConfirmButton: false,
          showCancelButton: false,
          center: true,
          type: "warning",
        });
      } else if (!this.inputPhoneNum && !this.excelNumbersText && !this.groupMembersNum) {
        this.$parent.concatsIsNull = true
      } else {
        let dailySendNums = this.$parent.dailySendNums;
        const {isReachSendLimit, showConfirmTitle} =
            this.$checkDailySendNums(dailySendNums);
        if (isReachSendLimit) {
          this.$confirm(showConfirmTitle, "", {
            confirmButtonText: "OK",
            type: "warning",
            showClose: false,
            showCancelButton: false,
            customClass: "daily-msg-send-warning",
          });
          return;
        }
        this.$parent.sendMessageType = this.sendMessagePro;
        this.$sendLog(302309, {s_amended_count: this.proChangeNum})
        this.proChangeNum = 0
        chrome.storage.local.remove(['proChangeNum', 'confirmSendData', 'confirmSetData'])
        await this.$parent.handleConfirmClick()

        const _this = this;
        chrome.storage.local.get(['isShowSafetyWarningFlagValue','dialogVisible','confirmSetData','allDataImg','allDataVideo','allDataDocument','confirmSendData'], (res) => {
          if (res.isShowSafetyWarningFlagValue || res.isShowSafetyWarningFlagValue == undefined) {
            _this.$parent.isShowSafetyWarningFlagValue = true
          } else {
            _this.$parent.activeName = "statistics"
            if (res.dialogVisible === false) {
              if (res.confirmSetData) {
                chrome.storage.local.set(res.confirmSetData)
              }
              if ((typeof res.confirmSendData === 'object') && (res.confirmSendData !== null)) {
                let confirmSendData = res.confirmSendData
                confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : [];
                confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : [];
                confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : [];
                _this.$parent.sendDataPopToContent(res.confirmSendData)
              }
            } else {
              _this.$parent.dialogVisible = true
            }
          }
        })
      }
    },
    checkSpecialCharacters(dataTmp, data, a) {
      Object.keys(dataTmp).forEach((key, index) => {
        let keyA = key.replace(//g, '(').replace(//g, ')')
        if (keyA.indexOf(a) != -1) {
          data[a] = dataTmp[key];
        }
      });
      return data;
    },
    deleteAttachmentClick() {
      this.radio = '';
      this.attachmentDivHtml = '';
      chrome.storage.local.set({
        radio: '',
        attachmentDivHtml: '',
        allDataImg: [],
        allDataVideo: [],
        allDataDocument: []
      })
    }
  },
  created: async function () {
    const _this = this;
    chrome.storage.local.get(['whatsAppNumbers','messageText','minNum','maxNum','sendAttachmentsValue','radio','remindTitlePro','incorrectPhoneNumPro','inputPhoneNum','inputPhoneNumFlag','groupMembersNum','groupConcats','proChangeNum','attachmentDivHtml','stopFlag','uploadExcelValue','excelData','messageTextTemplateOptions','messageTextTemplate','apologizeForBotherValue','groupOptions','groupsMap','groupInfo'], (res) => {
      _this.whatsAppNumbers = res.whatsAppNumbers;
      _this.messageText = res.messageText || '';
      _this.minNum = res.minNum ? res.minNum : 6;
      _this.maxNum = res.maxNum ? res.maxNum : 10;
      _this.sendAttachmentsValue = res.sendAttachmentsValue;
      _this.radio = res.radio ? res.radio : '';
      _this.remindTitlePro = res.remindTitlePro || 'normal'
      _this.incorrectPhoneNumPro = res.incorrectPhoneNumPro || ''
      _this.inputPhoneNum = res.inputPhoneNum || 0
      _this.inputPhoneNumFlag = res.inputPhoneNumFlag || 'normal'
      _this.groupMembersNum = res.groupMembersNum || 0
      _this.groupConcats = res.groupConcats || 'normal'
      _this.proChangeNum = res.proChangeNum || 0
      _this.attachmentDivHtml = res.attachmentDivHtml ? res.attachmentDivHtml : ''
      if (_this.remindTitlePro !== 'normal') {
        let remindBorder = document.querySelector('.textareaBackPro.el-input.el-input--medium.el-input--suffix>.el-input__inner')
        remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
      }
      if (res.stopFlag === undefined) {
        _this.stopFlag = true;
      } else {
        _this.stopFlag = res.stopFlag;
      }

      if (res.uploadExcelValue === undefined) {
        _this.uploadExcelValue = true;
      } else {
        _this.uploadExcelValue = res.uploadExcelValue;
      }
      const sendData = {
        stopFlag: _this.stopFlag,
      };
      _this.$parent.sendDataPopToContent(sendData);

      _this.excelNumbersText = res.excelData
          ? res.excelData.length
          : "";
      _this.messageTextTemplateOptions = res.messageTextTemplateOptions || [
        {
          value:
              "Hello {{First Name}}.\n" +
              "There is a WhatsApp bulk message send tool I want to recommend to you. \n\n" +
              '1 If you are interested, please reply "Yes" and I will show you more details.\n' +
              '2 If it disturbs you, please reply "No" and I will not send you any more messages.\n',
          label:
              "Hello {{First Name}}.\n" +
              "There is a WhatsApp bulk message send tool I want to recommend to you. \n\n" +
              '1 If you are interested, please reply "Yes" and I will show you more details.\n' +
              '2 If it disturbs you, please reply "No" and I will not send you any more messages.\n',
        },
      ];
      _this.messageTextTemplate = res.messageTextTemplate;
      _this.apologizeForBotherValue = res.apologizeForBotherValue;
      _this.groupOptions =
          res.groupOptions !== undefined ? res.groupOptions : [];
      _this.groupsMap = res.groupsMap || {};
      _this.groupInfo = res.groupInfo || ''
    });
  },
};
</script>

