            <el-col><span class="titleText">Export group members</span></el-col>
            </el-row>

            <el-row :style="{ margin: '12px 0 0 3px' }">
                <el-col :span="15">
                    <el-select
                        class="groupToolSelected"
                        :class="{ groupUnselected: chooseWarning, groupRemoved: beRemovedWarning }"
                        v-model="exportGroupInfo"
                        placeholder="Select joined group to export excel"
                        size="mini"
                        :no-data-text="'No Data'"
                        @change="handleGroupChange"
                        @visible-change="handleGroupChangeLog"
                    >
                        <el-option
                        v-for="item in groupOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                        >
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="9" class="switchBack">
                    <el-switch
                        class="switch"
                        active-color="#37B64A"
                        inactive-color="#BABBBD"
                        @change="handleExcludeGroupAdminsValueChange"
                        v-model="excludeGroupAdminsValue"
                        active-text="Exclude group admins">
                    </el-switch>
                </el-col>
            </el-row>

            <div style="margin-left: 5px; height:12px" >
