                    </span>
                    <span v-show="beRemovedWarning" width="50%" style="color: #C12324;text-align: left;line-height: 12px;font-size: 12px;background-color: #FFFFFF">
                    <i class="exclamation">!</i> You were removed from the group chat.
                    </span>
                <!-- </el-col> -->
            </div>
            <el-button class="common-success-btn"  type="success" @click="exportClick" plain size="medium">
                    Download Excel
            </el-button>
