        <el-card style="height:261px">
            <!-- <el-divider></el-divider> -->
            <el-row style="margin-left: -7px;font-size: 14px;"><span class="titleText">Find more groups</span></el-row>
            <el-row style="line-height: 39px;margin-left: 10px">
            <el-col :span="1"><i class="el-icon-warning" style="color: #909399;font-size: 18px"></i></el-col>
            <el-col :span="23" style="font-size: 14px;">This feature can help you find more related groups.</el-col>
            </el-row>
            <el-row style="line-height: 39px;color: rgb(186, 187, 189);margin-left: 10px">
            <el-col :span="1"></el-col>
            <el-col :span="23" style="margin-left: 24px;font-size: 14px;">Under development now, please see the <a
                href="javascript:;" class="common-href-style"
                @click="goLinkTutorial('http://waplus.io/blog/find-more-groups-feature?utm_source=sender_extension')">tutorial</a>.
            </el-col>
            </el-row>
            <el-row style="line-height: 39px;color: rgb(186, 187, 189);margin-left: 10px">
            <el-col :span="1"></el-col>
            <el-col :span="23" style="margin-left: 24px;font-size: 14px;">
                If you have any questions, please <a href="javascript:;" class="common-href-style"
                                                    @click="goLinkContact('https://api.whatsapp.com/send/?phone=%2B8617891209984&text=Hi+WAPlus+Sender%21+I+am+waiting+for+%22find+more+groups%22+feature.')">contact
                us</a>.
            </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
import { getAllPhoneNumberList } from "@/utils/phone-number-util";
  export default {
    name:'GroupTools',
    props:['chooseWarning','beRemovedWarning',"groupOptions", "groupsMap"],
    data() {
        return {
            exportGroupInfo:'',
            excludeGroupAdminsValue: true,
        }
    },
    created() {
        const _this = this
        chrome.storage.local.get(['groupOptions','groupsMap','exportGroupInfo','excludeGroupAdminsValue'],res=>{
            _this.groupOptions =
                res.groupOptions !== undefined ? res.groupOptions : [];
            _this.groupsMap = res.groupsMap || {};
            _this.exportGroupInfo = res.exportGroupInfo || '';
            _this.excludeGroupAdminsValue =
            res.excludeGroupAdminsValue !== undefined
            ? res.excludeGroupAdminsValue : true;
        })
    },

    methods: {
        handleGroupChange(value) {
            let group = this.groupsMap[this.exportGroupInfo];
            chrome.storage.local.set({exportGroupInfo:this.exportGroupInfo})
            let phoneNumberList = getAllPhoneNumberList(group);    
            this.$parent.messageBoxClose();
            if (phoneNumberList.length === 0) {
                console.log(('be removed out the group'))
            }
        },
        handleGroupChangeLog(value){
          if(value){
            this.$sendLog(303205);
          }
        },
        exportClick() {
            // if(this.chooseWarning || this.beRemovedWarning) return false
            this.$sendLog(303201);
            let tmp = this.excludeGroupAdminsValue ? 'excludeAdmins' : 'all';
            const sendData = {exportGroup: tmp,exportGroupInfo:this.exportGroupInfo};
            this.$parent.sendDataPopToContent(sendData);
        },
        handleExcludeGroupAdminsValueChange(value) {
            console.log(value)
            this.$sendLog(303202);
            chrome.storage.local.set({excludeGroupAdminsValue: this.excludeGroupAdminsValue});
        },
        goLinkTutorial(link) {
            this.$sendLog(303203);
            window.open(link);
        },

        goLinkContact(link) {
            this.$sendLog(303204);
            window.open(link);
        }
    },
  }
</script>

