<template>
  <div>
      <el-row :style="{height: '45px',marginBottom:'-2px'}">
        <el-col :span="8">
          <p class="waplus-title"
             :style="{fontStyle: 'normal', fontSize: '18px', fontWeight: 'bold', color: '#37B64A', textAlign: 'left', backgroundColor: '#FFFFFF'}">
            {{ waPlusSenderText }}</p>
        </el-col>
        <el-col :span="8">
          <ZbaseTopAdv :browserType="browserType"></ZbaseTopAdv>
        </el-col>
        <el-col :span="8" class="topText">
          <el-header class="version-phone-font" v-text="versionText"></el-header>
          <el-footer class="version-phone-font" v-text="numberText" ></el-footer>
        </el-col>
      </el-row>
  </div>
</template>

<script>
import {WAPLUS_CRM_INSTALL_URL_TOP} from "@/utils/common/config";
import config from '../../../package.json'
import {ZbaseTopAdv} from "zbase-popup-component";
import {getBrowser} from "@/utils/browser-util";
  export default {
    name:'TopInfo',
    data() {
        return {
            waPlusSenderText: 'WAPlus Sender',
            versionText: 'Version: ',
            numberText: 'Number: + null',
            browserType: "Chrome"
        }
    },
    components: {
      ZbaseTopAdv,
    },
    created:async function() {
        this.browserType = getBrowser()['browser']
        const _this = this
        // this.versionText +=  chrome.runtime.getManifest().version
        this.versionText += config.version
        chrome.storage.local.get(['userPhoneNum'],res=>{
            if(res.userPhoneNum){
                _this.numberText = 'Number: +' + res.userPhoneNum;
            }
        })
    },
    methods: {
        openPageCRM() {

            // 300201
            this.$sendLog(300201);
            window.open(WAPLUS_CRM_INSTALL_URL_TOP);
        },
    },
  }
</script>

<style scoped>
.waplus-title {
    margin: 7px auto 7px 12px;
}
.version-phone-font {
  text-align: right;
  font-size: 12px;
  padding: 0;
  background-color: #FFFFFF;
  height: 17.5px !important;
  color: rgba(96, 98, 102, 100);
}
</style>
